import { ValidatorFn } from "@angular/forms";
import { Observable } from 'rxjs';
import { ApiResponse } from 'src/app/models/api-response.model';
import { LookupPropertyTypeEnum } from "./LookupPropertyType.enum";


export class LookupProperty {
    field: string;
    header: string;
    type: LookupPropertyTypeEnum;
    validation: {required?:boolean,pattern?:RegExp};
    dataSource: Observable<any[]>;
    dependOnDataSource: string;
    readOnly: boolean = false;
    hiddenInList: boolean = false;
    constructor(
        field: string,
        header: string,
        type: LookupPropertyTypeEnum,
        readOnly?: boolean,
        hiddenInList?: boolean,
        validation?: {required?:boolean,pattern?:RegExp},
        dependOnDataSource?: string
    ) {
        this.field = field;
        this.header = header;
        this.type = type;
        this.readOnly = readOnly
        this.validation = validation
        this.hiddenInList = hiddenInList
        this.dependOnDataSource = dependOnDataSource;
    }
}
