import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ConfirmationService, MessageService } from 'primeng/api';
import { DialogService, DynamicDialogConfig } from 'primeng/dynamicdialog';
import { forkJoin, Observable } from 'rxjs';
import { LookupService } from '../../services/lookup.service';
import { LookupFormComponent } from '../lookup-form/lookup-form.component';

@Component({
  selector: 'app-lookup',
  templateUrl: './lookup.component.html',
  styleUrls: ['../../../../demo/view/tabledemo.scss'],
    styles: [`
        :host ::ng-deep .p-dialog .product-image {
            width: 150px;
            margin: 0 auto 2rem auto;
            display: block;
        }
    `],
  providers: [
    MessageService,
    ConfirmationService,
    LookupService,
    DialogService
  ],
  entryComponents:[
      LookupFormComponent
  ]
})
export class LookupComponent implements OnInit {
  lookupType:string

  lookupDialog: boolean;

  lookupList: any[];

  newLookup: any;

  selectedLookups: any[];

  submitted: boolean;

  cols: TableColumn[]

  lookupDataCount : number

  pageNo: number = 1;

  constructor(
    public lookupService: LookupService,
    private messageService: MessageService,
    private confirmationService: ConfirmationService,
    private dialogService: DialogService,
    private activatedRoute : ActivatedRoute,
    private router:Router,
    ) {}

  ngOnInit() {
    this.activatedRoute.params.subscribe(
        (params)=>{
            // check if this is a valid params
            const entity:string = params.entity
            if(!entity || !this.lookupService.lookups[entity]){
                return this.router.navigate(['/notfound'])
            }
            this.lookupService.selectedEntity = this.lookupService.lookups[entity]
            this.lookupService.selectedEntityName = entity
            this.getDisplayedCols()
            this.getLookupList()
        }
    )

  }

  getDisplayedCols() {
    this.cols = []
    this.lookupService.selectedEntity.cols.forEach((col)=>{
        if(!col.hiddenInList){
            this.cols.push(new TableColumn(col.field,col.header))
        }
    })
  }
  getLookupList(){
    this.lookupService.getLookupList({ pageNumber : this.pageNo,pageSize:10 })
    .subscribe(
      (res:any)=>{
        this.lookupList = res.data
        this.lookupDataCount = res.count;
      }
    )
  }

  openlookupForm(lookup?:any) {
      let editMode :Boolean = Boolean(lookup)
      let config :DynamicDialogConfig =  new DynamicDialogConfig()
      config.closeOnEscape = false
    //   config.closable = false
    //   config.height = '500px'
      config.width = '400px'
      if(lookup) config.data = lookup
      config.contentStyle = {"min-height": "200px","max-height": "700px", "overflow": "auto"}
      config.baseZIndex = 10000
      config.header = `${editMode?'Edit':'Add new'}  ${this.lookupService.selectedEntityName}`
      this.dialogService.open(
          LookupFormComponent,
          config
      ).onClose.subscribe((res)=>{
          debugger
          if(res) this.getLookupList()
      })
  }

  deleteSelectedLookups() {
      this.confirmationService.confirm({
          message: 'Are you sure you want to delete the selected products?',
          header: 'Confirm',
          icon: 'pi pi-exclamation-triangle',
          accept: () => {
              const productsDeletedIDs :number[] = []
              let deleteRequests : Observable<any>[] = []
              if( this.selectedLookups && Array.isArray(this.selectedLookups) )this.selectedLookups.forEach((lookup)=>{
                deleteRequests.push(this.lookupService.deleteLookup(lookup.id))
                productsDeletedIDs.push(lookup.id)
              })
              this.selectedLookups = null;
              forkJoin(deleteRequests)
              .subscribe((res)=>{
                this.messageService.add({severity: 'success', summary: 'Successful', detail: `Items with Id/s (${productsDeletedIDs.toString()}) successfully deleted`, life: 3000});
              })
          }
      });
  }

  editLookup(lookup: any) {
    this.openlookupForm(lookup)
  }

  addLookup(){
    this.openlookupForm()
  }

  deleteLookup(lookup: any) {
      this.confirmationService.confirm({
          message: 'Are you sure you want to delete ' + lookup.name + '?',
          header: 'Confirm',
          icon: 'pi pi-exclamation-triangle',
          accept: () => {
            this.lookupService.deleteLookup(lookup.id).subscribe(data => {
                if (data.succeeded) {
                    debugger
                    this.getLookupList()
                    this.removeItemFromSelectedItems(lookup.id)
                    this.messageService.add({ severity: 'success', summary: 'Successful', detail: `Repair request with Id #${lookup.id} Successfully Deleted`, life: 3000 });
                }
            })
          }
      });
  }

  loadMoreListRequestData(event: any) {
    this.pageNo = event.page + 1;
    this.getLookupList();
  }


// refresh the selected items if the deleted item is already selected
  removeItemFromSelectedItems(id:number){
    if( this.selectedLookups && Array.isArray(this.selectedLookups) ){
        const filteredItems = this.selectedLookups.filter((_item:any)=>{
            return _item.id != id
        })
        this.selectedLookups = [...filteredItems]
    }
  }
}



export class TableColumn {
    field: string
    header:string
    constructor(field:string,header:string){
        this.header = header
        this.field = field
    }
}
