import { LookupProperty } from "./LookupProperty.model";
import { LookupPropertyTypeEnum } from './LookupPropertyType.enum';


export class LookupConfig {
    cols: LookupProperty[] = [
        new LookupProperty("id", "ID", LookupPropertyTypeEnum.inputNumber, true, true),
    ];
    endPointPrefix: string;
    name: string;
    path: string;
    constructor(
        name: string,
        path: string,
        endPointPrefix: string,
        cols: LookupProperty[],
    ) {
        cols.forEach((col) => this.cols.push(col));
        this.endPointPrefix = endPointPrefix;
        this.name = name;
        this.path = path;
    }
}
