import { Component, OnInit } from "@angular/core";
import { NgForm } from '@angular/forms';
import { MessageService, SelectItem } from 'primeng/api';
import { DynamicDialogRef, DynamicDialogConfig } from "primeng/dynamicdialog";
import { map } from 'rxjs/operators';
import { LookupProperty } from '../../models/LookupProperty.model';
import { LookupPropertyTypeEnum } from '../../models/LookupPropertyType.enum';
import { LookupService } from "../../services/lookup.service";

@Component({
    selector: "app-lookup-form",
    templateUrl: "./lookup-form.component.html",
    styleUrls: ["./lookup-form.component.scss"],
})
export class LookupFormComponent implements OnInit {
    inputTypesEnum = LookupPropertyTypeEnum
    submitted:boolean = false
    lookupData:any // if undefined that indicates that this is add new mode
    lookupDataFields:LookupProperty[] // if undefined that indicates that this is add new mode
    validlookupData
    supplimentaryDataSources = {}
    constructor(
        public ref: DynamicDialogRef,
        public config: DynamicDialogConfig,
        public lookupSrvc: LookupService,
        private messageService: MessageService,
    ) {}

    ngOnInit(): void {
        this.lookupData = this.config.data
        if(!this.lookupData) this.constructLookupObject()
        this.getLookupFieldsAsString()
        // let dataSourceRequests :{[key:string] : Observable<ApiResponse> } = {}
        this.lookupDataFields.forEach((col)=>{
            if(col.dependOnDataSource) {
                let listEndPoint = this.lookupSrvc.lookups[col.dependOnDataSource].endPointPrefix
                col.dataSource = this.lookupSrvc.getLookupList({noPaging:true,listEndPoint})
                .pipe(map((res:any)=>{
                    debugger
                    let selectItems:SelectItem[] = [{value:null,label:"None"}]
                    res.data.forEach(elem => {
                        selectItems.push({
                            value : elem.id,
                            label : elem.name
                        })
                    });
                    return selectItems
                }))
                // dataSourceRequests[col.dataSource] = this.lookupSrvc.getLookupList({noPaging:true,listEndPoint})
            }
        })
        // let requests = Object.values(dataSourceRequests)
        // if(requests.length){
        //     forkJoin(requests)
        //     .subscribe((res)=>{
        //         let dataSourceRequestsKeys = Object.keys(dataSourceRequests)
        //         res.forEach((res,index)=>{
        //             let keyName = dataSourceRequestsKeys[index]
        //             dataSourceRequests[keyName] = res.data
        //         })
        //     })
        // }
    }
    constructLookupObject(){
        this.lookupData = {}
        this.lookupSrvc.selectedEntity.cols.forEach((col)=>{
            this.lookupData[col.field] = null
        })
    }
    getLookupFieldsAsString(){
        this.lookupDataFields = this.lookupSrvc.selectedEntity.cols.reduce((accum,col)=> {
            debugger
            if(!col.readOnly) accum.push(col)
            return accum
        } ,[])
        this.validlookupData = this.lookupSrvc.selectedEntity.cols.filter((col)=> {
            return !col.readOnly
        })
    }
    addOrEditProduct(form:NgForm){
        debugger
        if(!form.valid){
            form.form.markAllAsTouched()
            return this.messageService.add({severity: 'error', summary: 'Error', detail: `Complete All the required fields first`, life: 3000});
        }
        debugger
        let data : any = {}
        this.lookupDataFields.forEach((row)=>{
            data[row.field] = this.lookupData[row.field]
        })
        if(this.lookupData.id) {
            data.id = this.lookupData.id
            this.lookupSrvc.editLookup(data)
            .subscribe((res)=>{
                this.close()
                this.messageService.add({severity: 'success', summary: 'Successful', detail: `Saved Successfully`, life: 3000});
            })
        } else {
            this.lookupSrvc.addLookup(data)
            .subscribe((res)=>{
                this.close()
                this.messageService.add({severity: 'success', summary: 'Successful', detail: `Saved Successfully`, life: 3000});
            })

        }


    }
    close(){
        this.ref.close(true)
    }
}
